import Select from "../common/select.vue"
export default{
	components:{Select},
	data(){
		return{
			school:0,
			current:false,//全选
			checknum:0,
			checkList:[],//续费项目选择
			smList:[{
				bj:require('../../static/img/img/wd_016.png'),
				value:1,
				text:"早接",
				childName:"李晓鹏",
				xlList:['默认之前选择的线路','线路1','线路2','线路3'],
				endtime:'2020-12-30',
				money:'￥998.00'
			},{
				bj:require('../../static/img/img/wd_015.png'),
				value:2,
				text:"晚送",
				childName:"李晓鹏",
				xlList:['默认之前选择的线路','线路1','线路2','线路3'],
				endtime:'2020-12-30',
				money:'￥998.00'
			},{
				bj:require('../../static/img/img/wd_014.png'),
				value:3,
				text:"全包",
				childName:"李晓鹏",
				xlList:['默认之前选择的线路','线路1','线路2','线路3'],
				endtime:'2020-12-30',
				money:'￥998.00'
			}]
		}
	},
	methods:{
		allEle(){
			//全选
			this.current=!this.current
			this.checkList=[]
			if(this.current){
				let count=this.smList.length
				for(var i=0;i<count;i++){
					this.checkList.push(i)
				}
			}
		},
		changeList(val){
			console.log(val)
			if(this.checkList.indexOf(val)==-1){
				//选中
				this.checkList.push(val)
				if(this.checkList.length==this.smList.length){
					this.current=true//全选
				}
			}else{
				//取消
				let i=this.checkList.indexOf(val)
				this.checkList.splice(i,1)
				this.current=false//取消全选
			}
		},
		changNum(val){
			console.log('changenum')
			console.log(val)
			// this.checknum=val
			
		},
		Settlement(){
			// 结算
			uni.navigateTo({
				url:"../information/editchild?type=2"
			})
		},
	}
}
